html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block
}

html {
  height: 100%
}

body {
  line-height: 1
}

ol,
ul {
  list-style: none
}

blockquote,
q {
  quotes: none
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none
}

table {
  border-collapse: collapse;
  border-spacing: 0
}


//-----------------------------------------

.container {
  max-width: 1440px;
  margin: 0 auto;
  z-index: 999;
}

.headerContainer {
  perspective: 1500px;
}

//-----------------------------------------
.header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100vh;
  background-color: #0E3963;
  position: relative;

  // .header_title
  .particles-js-canvas-el {
    position: absolute;
    top: 0;
    left: 0;
  }

  &_title {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 64px;
    line-height: 78px;
    color: #FFFFFF;
    padding-top: 190px;
    margin-bottom: 50px;
    text-align: center;
  }

  &_e {
    display: inline-flex;
  }

  &_y1 {
    width: 35px;
    height: 35px;
    border: 7px solid white;
    border-radius: 50%;
    margin-right: 3px;
    position: relative;
  }

  &_y2 {
    width: 15px;
    height: 15px;
    background: white;
    position: absolute;
    left: 0px;
    bottom: 7.5px;
    border-radius: 100%;
  }

  &_y3 {
    width: 35px;
    height: 35px;
    border: 7px solid white;
    border-radius: 50%;
    position: relative;

  }

  &_y4 {
    width: 15px;
    height: 15px;
    background: white;
    position: absolute;
    left: 0px;
    bottom: 7.5px;
    border-radius: 100%;

  }

  // .header_gallery

  &_gallery {
    transform-style: preserve-3d;
  }

  // .header_me

  &_me {
    background-color: white;
    height: calc((1vh + 1vw) * 4);
    border-radius: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px calc((1vh + 1vw) * 3.2);
    margin-bottom: 62px;
    transition: opacity .5s ease;
    will-change: transform;
    transform-style: preserve-3d;
  }

  // .header_text

  &_text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: calc((1vh + 1vw) * 1.4);
    line-height: calc((1vh + 1vw) * 1.3);
    color: #000000;
    //padding-bottom: 5px;
    text-align: center;
  }

  &_text span {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: calc((1vh + 1vw) * 1.2);
    line-height: calc((1vh + 1vw) * 1.5);
    color: #000000;
  }

  // .header_arrow

  &_arrow {
    margin: 40px auto 30px auto;
    width: 38px;
    height: 100px;
    transition: opacity 1s ease;
  }

  &_arrow span {
    display: block;
    width: calc((1vh + 1vw) * 1.4);
    height: calc((1vh + 1vw) * 1.4);
    border-bottom: 3px solid #ffffff;
    border-right: 3px solid #ffffff;
    transform: rotate(45deg);
    margin: -16px 0;
    animation: arrow 2s infinite;
  }

  &_arrow span:nth-child(2) {
    animation-delay: -0.2s;
  }

  &_arrow span:nth-child(3) {
    animation-delay: -0.4s;
  }

  @keyframes arrow {
    0% {
      opacity: 0;
      transform: translate(0, -20px) rotate(45deg);

    }

    50% {
      opacity: 1;

    }

    100% {
      opacity: 0;
      transform: translate(0, 20px) rotate(45deg);

    }
  }
}

//-----------------------------------------

.services {

  // .services_info

  &_info {
    position: relative;
    padding: 350px 0px 100px 0px;
    margin-bottom: 300px;
  }

  &_info span:nth-child(1)>img {
    position: absolute;
    left: 3.95%;
    top: 30.21%;
    transform: rotate(11.42deg);
  }

  &_info span:nth-child(2)>img {
    position: absolute;
    left: 80.01%;
    top: 66.93%;
    transform: rotate(16.57deg);
  }

  &_info span:nth-child(3)>img {
    position: absolute;
    left: 3.15%;
    top: 90.32%;
    transform: rotate(-20.05deg);
  }

  // .services_li

  &_li {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 40px;
    line-height: 73px;
    color: #000000;
    margin-left: 10%;
    transition: opacity 1s ease;
  }

}

