.aboutUs {
  // .aboutUs_content

  &_content {
    text-align: left;

    position: relative;
  }

  &_text {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 44px;
    color: #000000;
    margin: 0px 0px 45px 0px;
