}

.slider {

  &_container {
    overflow: hidden;
  }

  // .slider_title

  &_title {
    height: 68px;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: calc((1vh + 1vw) * 1.5);
    line-height: calc((1vh + 1vw) * 0.9);
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &_titleContainer {
    background: #0E3963;
    border-radius: 11px;
    max-width: 700px;
    transform: translateX(-100%);
    transition: transform 3s;
  }

  // .slider_body
  &_body {
    display: flex;
    margin: 50px 0;
    padding: 30px 0;
  }

  // .slider_elem
  &_elem {
    // width: 397px;
    background: #FFFFFF;
    box-shadow: 0px 9px 30px rgba(0, 0, 0, 0.17);
    border-radius: 30px;
    //margin: 5px 30px 30px 30px;
  }

  .wrap{
    margin: 5px 30px 30px 30px;
  }

  //.slider_content
  &_content {
    padding: 0 20px;
  }
  // .slider_head
  &_head {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    color: #FFFFFF;
    background: #072B87;
    padding: 24px 0px 24px 41px;
    border-radius: 30px 30px 0px 0px;
    margin-bottom: 19px;
  }

  // .slider_service

  &_service {
    display: inline-block;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 15px;
    color: #FFFFFF;
    padding: 3px 14px;
    background: #072B87;
    border-radius: 4px;
    margin-bottom: 35px;
  }

  // .slider_spec

  &_spec {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    border-left: 6px solid #072B87;
    padding-left: 10px;
    margin-bottom: 23px;
  }

  // .slider_serviceName

  &_serviceName {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 27px;
    line-height: 33px;
    color: #0F2C47;
    margin-bottom: 67px;
  }

  // .slider_discription

  &_discription {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    display: flex;
    align-items: center;
    color: #414141;
    margin-bottom: 143px;
  }

  &_btnMore {
    position: relative;
    padding: 0px 0px 30px 45px;
    width: 100px;
    margin: 0 auto;
  }

  &_btnMoreCircle {
    content: '';
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #072B87;


  }

  &_btnMoreCircle::after {
    content: '';
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #072B87;
    margin-left: 15px;
  }

  &_btnMoreCircle::before {
    content: '';
    position: absolute;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #072B87;
    margin-left: 30px;

  }
}

.work {
  //work_body
  &_body{
    display: flex;
  }

  //work_title
  &_title{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: calc((1vh + 1vw) * 2);
    line-height: calc((1vh + 1vw) * 2.3);
    margin-bottom: 103px;
    hr{
      width: calc((1vh + 1vw) * 10);
      border-width: 3px;
      border-radius: 6px;
      margin: 0;
      color: #072B87;
    }
  }
  //work_circle
  &_circle{
    color: #FFFFFF;
    font-size: calc((1vh + 1vw) * 1.3);
    border-radius: 50%;
    width: 12em;
    height: 12em;
    background-color: #072B87;
    padding: 1.4644em;
    box-sizing: border-box;
  }
}